<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
  props: {
    probeType: {
      type: Number,
      default: 3
    },
    // pullUpLoad: {
    //   type: Boolean,
    //   default: true
    // },
     data: {
      type: Array,
      default: null
    },
  },
  data() {
    return {
      scroll: null
    }
  },
  mounted() {
    this.scroll = new BScroll(this.$refs.wrapper, {
      probeType: this.probeType,
      click: true,
      mouseWheel: true,
      // pullUpLoad: this.pullUpLoad
      
    })
    // this.scroll.on('pullingUp',()=>{
    //   this.$emit('pullingUp')
    // })
  },
  methods: {
    refresh() {
      this.scroll && this.scroll.refresh()
    }
  },
   watch: {
    data() {
      setTimeout(() => {
        this.refresh()
      }, 20)
    }
  }

}
</script>

<style scoped></style>
