import Vue from 'vue'
import VueRouter from 'vue-router'
const Register = () => import('@/components/content/register/register.vue')
const Recommend = () => import('@/views/recommend/Recommend.vue')
const PlaylistDetail = () => import('@/components/common/playlistDetail/playlistDetail.vue')
const Profile = () => import('@/views/profile/profile.vue')
const Search = () => import('@/views/search/search.vue')
const VideoDetail = () => import('@/views/video/detail/videoDetail.vue')
const Singer = () => import('@/views/singer/singer.vue')
const SingerDetail=()=>import('@/views/singer/detail/singerDetail.vue')
const SongSheet=()=>import('@/views/songSheet/songSheet.vue')
const Video = () => import('@/views/video/video.vue')
const MV = () => import('@/views/mv/mv')
const MvDetail = () => import('@/views/mv/detail/mvDetail.vue')
const Rank =()=>import('@/views/rank/rank.vue')

Vue.use(VueRouter)

// 解决跳转同一个路由操作
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    redirect: '/recommend'
  },
  { path: '/recommend', component: Recommend },
  {
    path: '/register',
    component: Register
  },
  {
    path: '/playlistdetail',
    component: PlaylistDetail
  },
  {
    path: '/profile',
    component: Profile,
    meta: {
      istoken: true
    }
  },
  {
    path: '/serach',
    component: Search
  },
  {
    path: '/videoDetail',
    component: VideoDetail
  },
  {
    path: '/singer',
    component: Singer
  },
  {
    path: '/singerdetail',
    component: SingerDetail
  },
  {
    path: '/songsheet',
    component: SongSheet
  },
  {
    path: '/video',
    component: Video
  },
  {
    path: '/mv',
    component: MV
  },
  {
    path: '/mvDetail',
    component: MvDetail
  },
  {
    path: '/rank',
    component: Rank
  }
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  const token = sessionStorage.getItem('token')
  const id = sessionStorage.getItem('id')
  if (!token && !id && to.meta.istoken) {
    next('/recommend')
    Vue.prototype.$message.error('请重新登录!')
  } else {
    next()
  }
})

export default router
