import { axios } from './axios'
export function playlistDetail(id, s) {
  return axios({
    url: '/playlist/detail',
    params: {
      id,
      s
    }
  })
}
// 获取歌曲详情
export function getSongDetail(ids) {
  return axios({
    url: '/song/detail',
    params: {
      ids
    }
  })
}
// 歌单收藏着
export function getSubscribers(id) {
  return axios({
    url: '/playlist/subscribers',
    params: {
      id
    }
  })
}
// 获取相关歌曲
export function getRelated(id, limit) {
  return axios({
    url: '/related/playlist',
    params: {
      id,
      limit
    }
  })
}
// 歌单评论
export function getComment(id, limit, offset) {
  return axios({
    url: '/comment/playlist',
    params: {
      id,
      limit,
      offset
    }
  })
}
// 获取音乐url
export function getMusicUrl(id) {
  return axios({
    url: '/song/url',
    params: {
      id
    }
  })
}
// 歌词
export function getLyric(id) {
  return axios({
    url: '/lyric',
    params: {
      id
    }
  })
}
