// 过滤mv信息
export class videoInfo {
  constructor(video) {
    this.id = video.vid||video.id
    this.img = video.coverUrl || video.imgurl || video.cover
    this.title = video.title||video.name
    this.count = video.playTime || video.playCount
    this.time = video.durationms || video.duration
    this.creator = fliterSinger(video.creator) || video.artistName
  }
}
// 过滤歌手

function fliterSinger(singer) {
  let res = []
  if (!singer) {
    return ''
  }
  singer.forEach(item => {
    res.push(item.userName||item.name)
  })
  return res.join('/')
}
