import Axios from 'axios'
export function axios (config) {
  const instance = Axios.create({
    baseURL: 'https://api.mtnhao.com/',
    timeout: 5000
  })
  instance.interceptors.request.use(
    config => {
      return config
    },
    err => {
      console.log(err)
    }
  )
  instance.interceptors.response.use(
    (config) => {
      return config
    },
    (err) => {
      console.log(err)
    }
  )
  return instance(config)
}
