import * as types from './mutatinon-const'
import state from './state'
export default {
  // 设置登录状态
  [types.SET_LOGINSTATE](state, payload) {
    state.loginState = payload
  },

  [types.SET_SINGER]() {},
  // 播放状态
  [types.SET_PLAYSTATE](state, payload) {
    state.playState = payload
  },

  // 播放列表
  [types.SET_PLAYLIST](state, payload) {
    state.playList = payload
  },
  // 顺序播放列表
  [types.SET_SEQUENCELIST](state, payload) {
    state.sequencePlayList = payload
  },
  // 播放模式
  [types.SET_PLAYMODE](state, payload) {
    state.mode = payload
  },
  // 当前播放索引
  [types.SET_CURRENTINDEX](state, payload) {
    state.currentPlayIndex = payload
  },
  // 历史播放记录
  [types.SET_HISTORY_LIST](state, payload) {
    state.historyList = payload
  },
  // 设置暂停状态
  [types.SET_PAUSE_STATE](state, payload) {
    state.playState = payload
  },
  [types.SET_SEARCH_LIST](state, payload) {
    state.searchList = payload
  },
  [types.SET_SINGERINFO](state, payload) {
    state.singerInfo = payload
  },
  // 用户信息
  [types.SET_USERINFO](state, payload) {
    console.log(payload)
    state.userInfo = payload
  }
}
