<template>
  <div class="search" @mouseleave="blur">
    <div class="item">
      <input class="text" type="text" :placeholder="placeholder" @focus="focus" v-model="query" @keyup.enter="search" />
      <span class="icon" @click="search"></span>
    </div>

    <transition name="asd">
      <div class="box" v-show="isshow">
        <div class="title-history">
          <div class="title"><i class="history-icon"></i><span class="history"> 历史搜索</span><span class="empty" @click="clearEmpty">清空</span></div>
          <div class="tag">
            <span class="tag-item" v-for="(item, index) in history" :key="index" @click="pushKey(item)">{{ item }} <i class="el-icon-close" @click="delItem(item)"></i> </span>
          </div>
        </div>
        <div class="hot-search">
          <div class="hot-title"><i class="hot-icon"></i><span class="hot"> 热门搜索</span></div>
          <div class="tag">
            <span class="tag-item" v-for="(item, index) in hotSearch" :key="index" @click="hotSearchItem(item.first)">{{ item.first }}</span>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { searchHot, searchSuggest } from '@/network/search'
import { mapActions, mapGetters } from 'vuex'
export default {
  props: ['placeholder'],
  data() {
    return {
      isshow: false,
      query: '',
      hotSearch: [],
      history:[]
    }
  },
  computed: {
    ...mapGetters(['searchList']),
    

    
  },

  created() {
    searchHot().then(res => {
      this.hotSearch = res.data.result.hots
    }),
    this.searchHistory()
  },
  methods: {
    ...mapActions(['seaveSearchHistory', 'deleteSearchItem', 'clearHistorySearch']),
    focus() {
      this.isshow = true
    },
    blur() {
      this.isshow = false
    },
    searchHistory(){
     this.history=JSON.parse(localStorage.getItem('__search__')) ||[]
    },
    search() {
      let keys = this.query.trim()
      if (keys.length !== 0) {
        this.seaveSearchHistory(keys)
        if (this.$router.path == '/serach') {
          this.$router.push({
            query: {
              keywords: keys
            }
          })
        } else {
          this.$router.push({
            path: '/serach',
            query: {
              keywords: keys
            }
          })
        }
      }
    },
    // 删除单个搜索记录
    delItem(item) {
      this.deleteSearchItem(item)
    },
    // 清空列表
    clearEmpty() {
      this.clearHistorySearch()
      this.query = ''
    },
    hotSearchItem(item) {
      this.query = item
      this.search()
      this.isshow = false
    },
    pushKey(item) {
      this.query = item
      this.search()
      this.isshow = false
    }
  }
}
</script>

<style scoped lang="less">
.search {
  z-index: 99999;
  position: relative;
  width: 280px;
  .item {
    position: relative;
    line-height: 70px;
    font-size: 14px;
    .text {
      padding: 5px 5px;
      width: 280px;
      border: 1px solid #ccc;
      border-radius: 5px;
    }
    .icon {
      width: 25px;
      height: 25px;
      background: url('~@/assets/img/header/search.png') no-repeat;
      background-size: 20px 20px;
      position: absolute;
      top: 25px;
      right: 6px;
    }
  }
  .box {
    width: 280px;
    height: 300px;
    background-color: #fff;
    position: absolute;
    top: 49px;
    .title-history {
      width: 100%;
      padding: 10px 15px;
      font-size: 14px;
      .title {
        .history-icon {
          width: 24px;
          height: 24px;
          background: url('~@/assets/img/header/2.png') no-repeat;
          background-size: 16px 18px;
          position: absolute;
        }
        .empty {
          float: right;
        }
      }
    }
    .hot-search {
      position: absolute;
      top: 150px;
      width: 100%;
      padding: 10px 15px;
      font-size: 14px;
      .hot-title {
        .hot-icon {
          width: 24px;
          height: 24px;
          background: url('~@/assets/img/header/3.png') no-repeat;
          background-size: 16px 18px;
          position: absolute;
        }
      }
    }
    .history,
    .hot {
      margin-left: 25px;
    }
    .tag {
      width: 100%;
      height: 110px;
      margin-top: 8px;
      color: #ccc;
      overflow: hidden;
      .tag-item {
        display: inline-block;
        padding: 5px;
        margin: 5px;
        color: #6d7685;
        background-color: #f4f4f5;
        border-radius: 5px;
      }
      .text:focus {
        outline: 0;
      }
    }
  }
  .asd-enter,
  .asd-leave-to {
    opacity: 0;
  }
  .asd-enter-active,
  .asd-leave-active {
    transition: all 1s;
  }
}
</style>
