<template>
  <div id="play" v-if="Object.keys(currenSong).length!==0">
   
    <div class="blumimg">
      <img :src="currenSong.songImg" alt="" />
    </div>
    <div class="info">
      <h3 class="singer ellipsis">{{ currenSong.songname }}</h3>
      <div class="song-name ellipsis">{{ currenSong.singer }}</div>
    </div>
    <div class="icon">
      <span class="pre iconfont icon-shangyishou" @click="pre"> </span>
      <span class="play" @click="changPlayState" :class="playIcon"></span>
      <span class="next  iconfont icon-xiayishou" @click="next"> </span>
    </div>
    <div class="progress-wrap">
      <span class="current-time">{{ currentTime | formatTime }}</span>
      <progress-item class="step" :pervent="pervent" @changto="clclick" />
      <span class="time">{{ currenSong.time | dateFormat('MM:SS') }}</span>
    </div>
    <div class="volume-box">
      <span class=" iconfont volume" :class="iconVolume" @click="changVolume"></span>
      <progress-volume class="volume-progress" ref="volume" @changto="toVolume"></progress-volume>
    </div>
    <div class="tool">
      <span class="iconfont  loop" :class="iconMode" @click="changMode"></span>
      <span class="iconfont icon-ci lyric" @click="lyricList"></span>
      <span class="iconfont icon-yinleliebiao   list " @click="musicList"></span>
    </div>
    <play-music-list v-show="isMusicList" :currentId="id" />
    <lyric-list ref="lyricList" :currentLyric="currentLyric" :currentLineNum="currentLineNum" v-show="isLyricList" />
    <audio ref="audio" :src="musicUrl" @canplay="ready" @error="error" @timeupdate="upDateTime" @ended="end"></audio>
  </div>
</template>

<script>
import progressItem from '@/components/common/progress/progress'
import progressVolume from '@/components/common/progress/progress'
import playMusicList from '@/components/content/playMusicList/playMusicList'
import lyricList from '@/components/content/lyric/lyric'
import Lyric from 'lyric-parser'

import { getMusicUrl, getLyric } from '@/network/detail'
import { playMode } from '@/common/playmode'
import { mapGetters, mapActions, mapMutations } from 'vuex'
export default {
  components: {
    progressItem,
    playMusicList,
    lyricList,
    progressVolume
  },

  computed: {
    // 计算进度
    pervent() {
      return this.currentTime / Math.round(this.currenSong.time / 1000)
    },
    // 播放状态
    playIcon() {
      return this.playState ? 'el-icon-video-pause' : 'el-icon-video-play'
    },
    // 设置循环ICON
    iconMode() {
      return this.mode === playMode.sequence ? 'icon-xunhuan' : this.mode === playMode.loop ? 'icon-loop-2' : 'icon-suiji'
    },
    iconVolume() {
      return this.isVolume ? 'icon-wusheng' : ' icon-shengyinyinliangxianxing'
    },
    ...mapGetters(['currenSong', 'playState', 'currentPlayIndex', 'playList', 'mode', 'sequencePlayList'])
  },

  watch: {
    // 获取当前歌曲的url
    currenSong(newSong, oldSong) {
      this.id = newSong.id
      getMusicUrl(newSong.id).then(res => {
        // 暂停点击播放模式会播发
        // if(newSong.id===oldSong.id)return
        this.musicUrl = res.data.data[0].url
        // 歌词重置
        if (this.currentLyric) {
          this.currentLyric.stop()
          // this.currentTime = 0
          // this.currentLineNum = 0
        }
        this.$nextTick(() => {
          this.$refs.audio.play()
          this.$refs.volume.setPercent(this.volume)
          this.saveHistoryList(newSong)
        })
      })

      this.getLyric(newSong.id)
    },
    playState(info) {
      const audio = this.$refs.audio
      if (audio) {
        info ? audio.play() : audio.pause()
      }
    }
  },

  data() {
    return {
      musicUrl: '',
      currentTime: 0,
      isMusicList: false,
      songReady: false,
      isLyricList: false,
      currentLyric: null,
      currentLineNum: 0,
      isVolume: false,
      volume: 0.2,
      id: ''
    }
  },

  methods: {
    ...mapActions(['saveHistoryList']),
    ...mapMutations({
      setPlayState: 'SET_PLAYSTATE',
      setCueerenIndex: 'SET_CURRENTINDEX',
      setMode: 'SET_PLAYMODE',
      setPlayList: 'SET_PLAYLIST'
    }),
    clclick(scale) {
      let cuTi = scale * this.$refs.audio.duration
      this.$refs.audio.currentTime = cuTi
      if (this.currentLyric) {
        console.log(this.currentLyric);
        this.currentLyric.seek(cuTi * 1000)
      }
    },
    toVolume(scale) {
      this.$refs.audio.volume = scale
    },
    // 显示播放列表
    musicList() {
      this.isMusicList = !this.isMusicList
      this.isLyricList = false
    },
    // 显示歌词
    lyricList() {
      this.isLyricList = !this.isLyricList
      this.isMusicList = false
    },
    // 暂停播放
    changPlayState() {
      if (!this.songReady) return
      this.setPlayState(!this.playState)
      if (this.currentLyric) {
        this.currentLyric.togglePlay()
      }
    },
    changVolume() {
      this.isVolume = !this.isVolume

      if (this.isVolume) {
        this.$refs.audio.volume = 0
      } else {
        this.$refs.audio.volume = 0.2
        this.$refs.volume.setPercent(0.2)
      }
    },
    next() {
      if (!this.songReady) return

      let index = this.currentPlayIndex + 1
      if (index === this.playList.length) {
        index = 0
      }
      this.setCueerenIndex(index)
      if (!this.playState) {
        this.changPlayState()
      }
      this.songReady = false
    },
    pre() {
      if (!this.songReady) return
      let index = this.currentPlayIndex - 1
      if (index === -1) {
        index = this.playList.length - 1
      }
      this.setCueerenIndex(index)
      if (!this.playState) {
        this.changPlayState()
      }
      this.songReady = false
    },
    // 切换播放模式
    changMode() {
      console.log(this.currenSong)
      const mode = (this.mode + 1) % 3
      this.setMode(mode)
      let list = null
      if (mode === playMode.random) {
        list = this.utils.shuffle(this.sequencePlayList)
      } else {
        list = this.sequencePlayList
      }

      // 以下两个函数调用顺序变化，会造成点击模式播放会随机播放歌曲
      this.resetCurrentIndex(list)
      this.setPlayList(list)
    },
    // 重置当前歌曲索引
    resetCurrentIndex(list) {
      let index = list.findIndex(item => {
        return item.id === this.currenSong.id
      })
      this.setCueerenIndex(index)
    },
    // 歌曲加载时间准备
    ready() {
      this.songReady = true
    },
    error() {
      this.songReady = true
    },
    // 播放进度时间
    upDateTime(e) {
      this.currentTime = e.target.currentTime
    },
    // 歌曲结束后
    end() {
      if (this.mode === playMode.loop) {
        this.loop()
      } else {
        this.next()
      }
    },
    loop() {
      this.$refs.audio.currentTime = 0
      this.$refs.audio.play()
      if (this.currentLyric) {
        // 结束后返回第一行歌词
        this.currentLyric.seek(0)
      }
    },
    // 获得歌词
    getLyric(id) {
      getLyric(id).then(res => {
        let lrc = res.data.lrc.lyric
        this.currentLyric = new Lyric(lrc, this.handleLyric)
        if (this.playState) {
          this.currentLyric.play()
        }
      })
    },
    // 歌词回调
    handleLyric({ lineNum, txt }) {
      this.currentLineNum = lineNum
      if (lineNum > 7) {
        let line = this.$refs.lyricList.$refs.lyric[lineNum - 7]
        this.$refs.lyricList.$refs.scroll.scroll.scrollToElement(line, 1000)
      } else {
        if (this.$refs.lyricList.$refs.scroll.scroll) {
          this.$refs.lyricList.$refs.scroll.scroll.scrollTo(0, 0, 1000)
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
#play {
  width: 100%;
  height: 70px;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-around;
  .blumimg {
    margin-left: 20px;
    display: flex;
    justify-items: center;
    img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
    }
  }
  .info {
    font-size: 14px;
    width: 200px;
    .singer,
    .song-name {
      margin: 15px;
    }
    .song-name {
      font-size: 12px;
      color: #999;
    }
  }
  .icon {
    font-size: 40px;
    display: flex;
    align-items: center;
    .play {
      margin: 0 15px;
    }
    .next,
    .pre {
      font-size: 30px;
      display: flex;
      align-items: center;
    }
  }
  .progress-wrap {
    margin-left: 70px;
    display: flex;
    align-items: center;
    .step {
      width: 600px;
      margin: 0 25px;
    }
  }
  .volume-box {
    display: flex;
    align-items: center;
    margin-left: 30px;
    .volume {
      font-size: 25px;
    }
    .volume-progress {
      margin-right: 60px;
      width: 150px;
    }
  }

  .tool {
    width: 190px;
    display: flex;
    .loop,
    .list,
    .lyric {
      width: 25px;
      margin: 0 15px;
      font-size: 25px;
    }
  }
}
</style>
