<template>
  <div id="login">
    <div class="login">
      <div @click="haha">登录</div>
    </div>
    <div class="login-warp" v-show="isLogin">
      <div class="overlay">
        <div class="conent">
          <div class="title">
            <img src="@/assets/img/header/title.jpg" alt="" />
          </div>
          <span class="close" @click.stop="close">
            <img src="@/assets/img/header/close.svg" alt="" />
          </span>
          <el-form :model="model" :rules="loginRules" ref="ruleForm">
            <el-form-item prop="phone">
              <el-input prefix-icon="el-icon-phone" v-model="model.phone"></el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" prefix-icon="el-icon-lock" v-model="model.password"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="subFrom" @keyup.enter="subFrom">登录</el-button>
              <el-button @click.stop="linkTo">注册</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { cellphone, getUserDetail } from '@/network/login'

import { mapMutations, mapGetters } from 'vuex'
export default {
  data() {
    const checkMobile = (rule, value, callback) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[0-9]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (!regMobile.test(value)) {
        callback(new Error('请输入正确的手机号码'))
      }
      callback()
    }
    return {
      isLogin: false,
      model: {
        phone: '',
        password: ''
      },
      loginRules: {
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          //  自定义手机号验证规则

          { validator: checkMobile, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {},
  methods: {
    haha() {
      this.isLogin = true
    },
    close() {
      this.isLogin = false
    },

    subFrom() {
      this.$refs.ruleForm.validate(val => {
        if (!val) return
        cellphone(this.model.phone, this.model.password).then(res => {
          if (res.data.code !== 200) return this.$message.error('登录手机或密码错误')
          console.log(res);
        sessionStorage.setItem('userInfo', JSON.stringify(res.data.profile))
       
        
        
            this.getUser(res.data.profile.userId)
          
          sessionStorage.setItem('loginState', true)
          sessionStorage.setItem('token', res.data.token)
          sessionStorage.setItem('id', res.data.profile.userId)
          this.setLoginState(localStorage.getItem('loginState'))
          // sessionStorage.setItem('userinfo', JSON.stringify(res.data.profile))
          this.isLogin = false
          window.location.reload()
        })
      })
    },

    // 获取个人信息
    getUser(id) {
      getUserDetail(id).then(res => {
        let userInfo = res.data.profile
        userInfo.level = res.data.level
        userInfo.createDays = res.data.createDays
        userInfo.createTime = res.data.createTime
        userInfo.listenSongs = res.data.listenSongs
        console.log(userInfo)
        this.userinfo(res.data.profile)
      })
    },
    linkTo() {
      this.$router.push('/register')
      this.isLogin = false
      console.log(this.isLogin)
    },
    ...mapMutations({
      userinfo: 'SET_USERINFO',
      setLoginState: 'SET_LOGINSTATE'
    })
  },
  computed: {
    ...mapGetters(['loginState'])
  }
}
</script>

<style scoped lang="less">
.login {
  display: flex;
  align-items: center;
  justify-content: center;

  .login-warp {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 199;
    .overlay {
      position: relative;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
      .conent {
        width: 500px;
        height: 350px;
        background-color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        .title {
          width: 150px;
          height: 150px;
          padding: 3px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .close {
          width: 25px;
          height: 25px;
          position: absolute;
          top: 0;
          right: 0;
          background-color: #ccc;
          border-radius: 50%;
          img {
            transform: translateY(-15px);
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
</style>
