import { axios } from './axios'
export function cellphone (phone, password) {
  return axios({
    url: '/login/cellphone',
    // method: 'post',
    params: {
      phone,
      password
    }
  })
}
// 发送验证码
export function sendCaptchaCode (phone) {
  return axios({
    url: '/captcha/sent',
    params: {
      phone
    }
  })
}
// 验证验证码
export function verify (phone, captcha) {
  return axios({
    url: '/captcha/verify',
    params: {
      phone,
      captcha
    }
  })
}
// 注册
export function register (captcha, phone, password, nickname) {
  return axios({
    url: '/register/cellphone',
    parsam: {
      captcha,
      phone,
      password,
      nickname
    }
  })
}
// 检测手机号码是否已注册
export function checkPhone (phone) {
  return axios({
    url: '/cellphone/existence/check',
    params: {
      phone
    }
  })
}


// 获取用户详情
export function getUserDetail (uid){
  return axios({
    url: '/user/detail',
    params:{
      uid
    }
  })
}
// 退出
export function logout(){
  return axios({
    url: '/logout'
  })
}
