import { axios } from './axios'
// 热搜
export function searchHot() {
  return axios({
    url: '/search/hot',
    
  })
}
// 类型搜索
export function allSearch(keywords, limit, offset, type) {
    return axios({
      url: '/search',
      params: {
        keywords,
        limit,
        offset,
        type
      }
    })
}
// 搜索关键词
export function searchSuggest(keywords, limit, offset, type) {
  return axios({
    url: '/search/suggest',
    params: {
      keywords,
      limit,
      offset,
      type
    }
  })
}
