<template>
  <div class="progress" ref="progress" @click="topre">
    <div class="line" ref="line">
      <div class="circle" ref="circle"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['pervent'],

  watch: {
    pervent(info) {
      let wrapWidth = this.$refs.progress.offsetWidth
      let setWidth = info * wrapWidth
      this.offset(setWidth)
    }
  },
  methods: {
    topre(e) {
      this.offset(e.offsetX)
      let wrapWidth = e.clientX - this.$el.offsetLeft
      let setWidth = (wrapWidth / this.$el.offsetWidth).toFixed(2)
      this.$emit('changto', setWidth)
    },
    offset(setWidth) {
      this.$refs.line.style.width = `${setWidth}px`
      this.$refs.circle.style.transform = `translateX(${setWidth}px)`
    },
    // 设置音量
    setPercent(info){
      let setWidth =info*this.$el.offsetWidth;
      this.offset(setWidth)
    }
  }
}
</script>

<style scoped lang="less">
.progress {
  height: 3px;
  border-radius: 2px;
  cursor: pointer;
  background: #454546;
  position: relative;
  .line {
    width: 10px;
    height: 100%;
    background: #b82525;
    .circle {
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background: #000;
      position: absolute;
      top: 0px;
      bottom: 0px;
      margin: auto;
    }
  }
}
</style>
