<template>
  <div id="header">
    <div class="limit flex">
      <div class="logo flex">
        <router-link to="/recommend" class="link-to"> <h1></h1></router-link>
      </div>
      <ul class="nav flex">
        <li><router-link to="/recommend" tag="a">个人推荐</router-link></li>
        <li><router-link to="/singer" tag="a">歌手</router-link></li>
        <li><router-link to="/songsheet" tag="a">歌单</router-link></li>
        <li><router-link to="/rank">排行榜</router-link></li>
        <!-- <li><router-link to="video">小视频</router-link></li> -->
        <li><router-link to="mv">MV</router-link></li>
      </ul>
      <search :placeholder="'搜索音乐、MV、歌单、用户'"></search>
      <div class="client">客户端</div>
      <div class="login">
        <div v-if="state" class="ho">
          <img @click="profile" :src="user.avatarUrl" alt="" class="login-img" />
          <div class="user-info">
            <div class="signout" @click="logout">
              <i class="iconfont icon-tuichu"></i>
              退出登录
            </div>
          </div>
        </div>
        <login v-else></login>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'

import { logout } from '@/network/login'
import login from '@/components/content/login/login'
import search from '@/components/content/search/search'
export default {
  data() {
    return {
      user: [],
      state: ''
    }
  },
  components: {
    search,
    login
  },
  computed: {
    ...mapGetters(['loginState', 'userInfo'])
    
  },
  watch: {
   
  },
  created() {
    this.getData()

  },
  methods: {
    profile() {
      this.$router.push('/profile')
    },
    getData() {
        this.$nextTick(function() {
        this.user = JSON.parse(sessionStorage.getItem('userInfo'))||this.userInfo
        this.state = sessionStorage.getItem('loginState')
     })
    },
    logout() {
      logout().then(res => {
          this.$router.push('/recommend')
          // window.sessionStorage.clear()
          window.location.reload()
      })
    }
  }
}
</script>

<style lang="less" scoped>
#header {
  width: 100%;
  height: 70px;
  background-color: #fff;
}
.logo {
  width: 170px;
  height: 70px;
  align-items: center;
  .link-to {
    width: 100%;
    height: 100%;
    background: url('~@/assets/img/header/logo.png') no-repeat;
    background-size: 170px 46px;
    background-position: 0px center;
  }
}
.nav {
  width: 500px;
  padding-left: 20px;
  text-align: center;

  li {
    width: 150px;
    line-height: 70px;

    font-size: 15px;
  }
  li a:hover {
    color: #fa2800;
  }
  .router-link-active {
    position: relative;
    color: #fa2800;
    padding: 10px 0;
    border-bottom: 1px solid pink;
  }
}

.client {
  text-align: center;
  line-height: 70px;
  width: 120px;
}
.ho:hover {
  .user-info {
    display: block;
  }
}
.login {
  position: relative;
  text-align: center;
  line-height: 70px;
  width: 120px;
  .login-img {
    margin-top: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50px;
  }

  .user-info {
    width: 120px;
    height: 100px;
    border: 1px solid #eee;
    background-color: #fff;
    position: absolute;
    top: 70px;
    left: 35px;
    z-index: 9;
    display: none;
    .signout:hover {
      color: red;
    }
  }
}
</style>
