import storage from 'good-storage'
const HISTORY_KEY = '__history__'
const HISTORY_MAX_LENGTH = 50
const SEARCH_KEY = '__search__'
const SEARCH_MAX_LENGTH = 10
function insertArray(arr, val, compare, maxLen) {
  const index = arr.findIndex(compare)
  if (index === 0) {
    return
  }
  if (index > 0) {
    arr.splice(index, 1)
  }
  arr.unshift(val)
  if (maxLen && arr.length > maxLen) {
    arr.pop()
  }
}

function deleteElement(arr, compare) {
  let index = arr.findIndex(compare)
  arr.splice(index, 1)
}

// 添加最近播放列表
export function saveHistory(song) {
  let songs = storage.get(HISTORY_KEY, [])
  insertArray(
    songs,
    song,
    item => {
      return song.id === item.id
    },
    HISTORY_MAX_LENGTH
  )
  storage.set(HISTORY_KEY, songs)
  return songs
}

// 清空历史播放列表
export function clearHistory() {
  storage.remove(HISTORY_KEY)
  return []
}
// 删除单个歌曲
export function deleteHistory(song) {
  let songs = storage.get(HISTORY_KEY, [])
  deleteElement(songs, item => {
    return song.id === item.id
  })
  storage.set(HISTORY_KEY, songs)
  return songs
}
// 保存搜索记录

export function saveSearch(query) {
  let search = storage.get(SEARCH_KEY, [])
  insertArray(
    search,
    query,
    item => {
      return item === query
    },
    SEARCH_MAX_LENGTH
  )
  storage.set(SEARCH_KEY, search)
  return search
}
// 清空历史搜索
export function clearSearch() {
  storage.remove(SEARCH_KEY)
  return []
}

// 删除搜索记录
export function deleteSearch(query) {
  let search = storage.get(SEARCH_KEY, [])
  deleteElement(search, item => {
    return item === query
  })
  storage.set(SEARCH_KEY, search)
  return search
}
