<template>
  <transition name="fate">
    <div class="list">
      <div class="title"><span>播放列表</span> <i class="el-icon-delete" @click="clearHistory"></i></div>
      <div class="play">
        <div class="item" v-for="(item, index) in historyList" :key="item.id" :class="currentId == item.id && playState ? 'active' : ''">
          <div class="num">
            <span class="inde">{{ index + 1 }}</span>
            <i class="pause-btn el-icon-video-pause" @click="pauseState"></i>
            <i class="play-btn el-icon-video-play" @click="changePlayState(item, index)"></i>
          </div>
          <div class="name">{{ item.songname }}</div>
          <span class="el-icon-close" @click="deleteItem(item)"></span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
  computed: {
    ...mapGetters(['historyList', 'playState'])
  },
  props: ['currentId'],
  methods: {
    ...mapActions(['deleteHistoryItem', 'selectPlay', 'clearHistoryList']),
    ...mapMutations({
      setPlayState: 'SET_PLAYSTATE',
      setPauseState: 'SET_PAUSE_STATE'
    }),
    clearHistory() {
      this.clearHistoryList()
    },
    deleteItem(item) {
      this.deleteHistoryItem(item)
    },
    changePlayState(item, index) {
      this.selectPlay({
        list: this.historyList,
        index
      })
    },
    pauseState() {
      this.setPauseState(false)
    }
  }
}
</script>

<style scoped lang="less">
.list {
  height: 580px;
  width: 390px;
  padding: 35px 0;
  background-color: #fff;
  position: absolute;
  bottom: 80px;
  right: 0;
  .title {
    display: flex;
    justify-content: space-between;
    margin: 0 20px 20px;
  }
  .play::-webkit-scrollbar {
    display: none;
  }
  .play {
    margin: 0 20px;
    overflow-y: scroll;
    height: 480px;
    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 30px;

      .num {
        margin: 8px 10px;
        width: 20px;
        text-align: center;
        .inde {
          font-size: 14px;
        }
        .pause-btn,
        .play-btn {
          color: #fa2800;
          font-size: 20px;
          display: none;
          cursor: pointer;
        }
      }

      .name {
        padding: 5px 30px 5px 5px;
        font-size: 14px;
        flex: 1;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }

  .item:hover {
    background-color: #fafafa;
    .num {
      .inde {
        display: none;
      }
      .play-btn {
        display: block;
      }
    }
  }
  .active:hover {
    .num {
      .inde {
        display: none;
      }
      .pause-btn {
        display: block !important;
      }
    }
  }
  .active {
    color: pink;
    .num {
      .inde {
        display: block;
      }
      .play-btn {
        display: none !important;
      }
    }
  }
}
</style>
